embla-slider {
  --gap: 0px;
  --slides-per-page: 1;
  --gaps-per-page: calc(var(--slides-per-page)  - 1);
}

embla-viewport {
  display: block;
  overflow: hidden;
}

embla-viewport.is-draggable {
  cursor: grab;
}

embla-viewport.is-dragging {
  cursor: grabbing;
}

embla-slides {
  align-items: flex-start;
  transition: height .2s;
  display: flex;
}

embla-slide {
  --total-gap: calc(var(--gaps-per-page) * var(--gap));
  flex: 0 0 calc(calc(100% - var(--total-gap)) / var(--slides-per-page));
  text-align: center;
  min-width: 0;
  display: block;
}

embla-slider[loop] embla-slide {
  margin-right: var(--gap);
}

embla-slider:not([loop]) embla-slides {
  gap: var(--gap);
}

embla-next, embla-prev {
  cursor: pointer;
  display: block;
}

embla-next[disabled], embla-prev[disabled] {
  cursor: not-allowed;
}

embla-progress {
  display: block;
}

embla-thumbnail {
  cursor: pointer;
  display: block;
}
